				<p>רבים מהמנכ"לים ומקבלי ההחלטות בעסקים, חברות וארגונים מוצפים בשיחות טלפון ומיילים שיווקיים באופן יומיומי. מעצם היותם כה עסוקים בכיבוי שריפות ובניהול השוטף, בהרבה מקרים הם אינם חוזרים לשיחות מכירה מעניינות, לא בגלל חוסר עניין אלא בגלל עומס רב ומחסור בזמן. לפיכך, אם צוות אנשי המכירות באמת מעוניין להפוך אותם ללקוחות, עליו לקדם פעולות הכרחיות של שיחות או שליחת דוא"ל לצורך מעקב ובמילים אחרות לנהל תהליך Follow Up.</p>
