<p>עודדו את המוטיבציה של אנשי המכירות באמצעות עבודת צוות מעת לעת. בנו יעדים לקבוצות או מחלקות ותמרצו אותם כיחידה אחת. ההפריה ההדדית של אנשי המכירות מאוד חשובה למקסום יחסי ההמרה לעסקאות בפועל. מידע שיתופי וסיעור מוחות בהחלט מסייעים להתמודד עם מקרים מורכבים ולהציל עסקאות "אבודות"…</p>
