<p>המונח שיווק B2C מתייחס לעסקים המשווקים מוצרים ו/או שירותים ו/או פתרונות לצרכנים פרטיים (ולא לעסקים). שיווק זה מייצר מכירות שהן ברוב המקרים בעלות תהליך מהיר יותר המתבססות על תהליכי קבלת ההחלטות מוטות רגש ותפיסת המוצר בעיני הלקוחות.</p>
