<p>יצירת בסיס ידע מרכזי ללקוחות הינו מהלך יזום חיוני מאוד בעידן החדש. בסיס זה יכול לייצר ערך מוסף ללקוחות ע"י הצגה של פתרונות ובעיות, מענה לשאלות, עשה ואל תעשה, סוגיות חדשות, הוראות הפעלה וכדומה.</p>
