<p>כמובן שאתם יכולים להחליט שתמונת המבט על הלקוח תוגבל רק לשירות ואז מערכת CSS תוכל לספק אתכם בהיבט זה. אולם שכשמדובר בעידן הנוכחי, במרבית המקרים אם לא בכולם, תמונת המבט הרצויה של אנשי השירות והתמיכה (כולל מהשטח באמצעות המובייל) היא 360 על הלקוח (מאפיינים, היסטוריה, מסמכים, הצעות מחיר ועוד), מומלץ לכם לבחור במערכת CRM מתקדמת.</p>
