				<p><span style="font-weight: 400;">העסק שלכם, הוא בדרך כלל גם ה"בייבי" שלכם: אתם משקיעים בו ימים כלילות, שוכרים עובדים המתמחים בתחומם ומחפשים דרכים חדשות להתקדם – בכדי לממש את מלוא הפוטנציאל שלכם.</span></p>
