				<p><span style="font-weight: 400;">השאיפה של כל עסק – היא להתפתח. קיימות אינספור דרכים להרחיב את העסק, כולן טקטיקות שמשתנות ומצריכות מגוון רחב של תנאים, דרכי פעולה, כסף, מאמץ וזיעה רבה.&nbsp;</span></p>
