<li style="font-weight: 400;"><span style="font-weight: 400;">הלקוחות הקיימים כבר רכשו מכם מוצרים או שירותים – כתוצאה מכך, הלקוחות הללו חקרו אתכם, ראו את מה שאתם מציעים ואהבו את זה. כיום, הם נהנים מהיתרונות של המוצר או השירות שסיפקתם להם כלומר, הם זוכרים אתכם לטובה וחשים ביטחון כלפיכם. מסיבות אלו, יהיה לכם הרבה יותר פשוט לעודד אותם לרכוש מכם בשנית. לאחר הרכישה השנייה, לקוחות רבים הופכים ללקוחות קבועים.</span></li>
