<p><span style="font-weight: 400;">כל המידע הזה מוזן ונשמר ב- CRM לנצח: המידע בדרך-כלל יגובה על ידי ענן או שרת פרטי, בכדי להבטיח כי המידע הזה יישמר לעד.</span></p>
