<p><span style="font-weight: 400;">מערכת ה- CRM אף יכולה לשלוח מיילים אישיים, שידגישו בפני הלקוחות שלכם עד כמה הם חשובים עבורכם: מיילים המברכים מזל טוב ביום ההולדת או ביום הנישואין, מיילים שמציעים ללקוח מוצר בהתאם לרכישות האחרונות שלו או מבצע שזמין אך ורק לו, כולם יעניקו לו תחושה כי הוא מיוחד, כי בחרתם במיוחד בו וכי אתם זוכרים אותו. ההערכה הזו תינטע בלקוח ביטחון ורצון לשוב ולרכוש מכם.</span></p>
