<p><span style="font-weight: 400;">רוב המוצרים והשירותים כיום, אינם נגמרים בהחלפת הכספים. בדרך כלל, יהיה על הלקוח להשקיע מזמנו בכדי ללמוד כיצד להשתמש במוצר, לבנות וליישם את האסטרטגיה, לעבוד על שינוי עצמי וללמוד להתאים את עצמו. בכל פעם, יהיה עליו לשוב ולהשקיע זמן ומחשבה.</span></p>
