<p><span style="font-weight: 400;">חלק מאנשי המכירות של היום מזניחים את המידע הזה, משום שהם חוששים שהלקוחות יירתעו מכך. מצד אחד, הם צודקים שכן איש לא אוהב להתחייב כיום. ועם זאת, כאשר אתם רוצים לגלות אם הלקוח שעומד מולכם אכן מעוניין במוצר שאתם מציעים, כדאי לכם לשאול את השאלה הזו, בכדי לגלות את התשובה טרם תחילת תהליך העבודה.&nbsp;</span></p>
