import img1 from "../../../assets/imgs/sales/Apple-iMac-forecast-600x789.png";
import SalesGetStarted from "../../../components/SalesGetStarted";
import { useTranslation } from "react-i18next";

const ForecastSales = () => {
  const { t } = useTranslation();
  return (
    <>
      <div className="product-page-container">
        <div className="product-section-container">
          <div className="right">
            <h1>{t("forecast-sales-title")}</h1>
            <div className="para">
              <h2>{t("forecast-sales-t-1")}</h2>
              <p>{t("forecast-sales-p-1")}</p>
            </div>
            <div className="para">
              <h2>{t("forecast-sales-t-2")}</h2>
              <p>{t("forecast-sales-p-2")}</p>
            </div>
            <div className="para">
              <h2>{t("forecast-sales-t-3")}</h2>
              <p>{t("forecast-sales-p-3")}</p>
            </div>
          </div>
          <div className="left">
            <img
              src={img1}
              alt="Forecast sales with Skyz CRM"
              title="Apple-iMac-forecast"
            />
          </div>
        </div>
        <SalesGetStarted />
      </div>
    </>
  );
};

export default ForecastSales;
