import img1 from "../../../assets/imgs/sales/mobile-access_new-600x747.png";
import SalesGetStarted from "../../../components/SalesGetStarted";
import { useTranslation } from "react-i18next";

const SkyzSalesMobile = () => {
  const { t } = useTranslation();
  return (
    <>
      <div className="product-page-container">
        <div className="product-section-container">
          <div className="right">
            <h1>{t("skyz-sales-mobile-title")}</h1>
            <div className="para">
              <h2>{t("skyz-sales-mobile-t-1")}</h2>
              <p>{t("skyz-sales-mobile-p-1")}</p>
            </div>
            <div className="para">
              <h2>{t("skyz-sales-mobile-t-2")}</h2>
              <p>{t("skyz-sales-mobile-p-2")}</p>
            </div>
            <div className="para">
              <h2>{t("skyz-sales-mobile-t-3")}</h2>
              <p>{t("skyz-sales-mobile-p-3")}</p>
            </div>
          </div>
          <div className="left">
            <img
              src={img1}
              alt="Skyz Sales mobile access"
              title="mobile-access_new"
            />
          </div>
        </div>
        <SalesGetStarted />
      </div>
    </>
  );
};

export default SkyzSalesMobile;
